import Vue from 'vue'
import Vuex from 'vuex'
import {
  model,
  modelAdd,
  imgListModel
} from "./mock.js";



Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentModel: model,
    currentImgListModel: imgListModel,
    /* Add */
    currentModelAdd: modelAdd,
    currentImgListModelAdd: [...imgListModel] /* copy */
  },
  actions: {
    setCurrentPath({
      commit
    }, path) {
      return commit('setCurrentPath', path)
    }
  },
  mutations: {
    setCurrentModel(state, model) {
      state.currentModel = model;
    },
    setCurrentImgListModel(state, model) {
      state.currentImgListModel = model;
    },
    /* Add */
    setCurrentModelAdd(state, modelAdd) {
      state.currentModelAdd = modelAdd;
    },
    setCurrentImgListModelAdd(state, modelAdd) {
      state.currentImgListModelAdd = modelAdd;
    },
  }
})