<template>
  <div class="view column">
    <main class="main">
      <div class="tool-bar fixed">
        <el-button @click="goTo('home')">放弃编辑</el-button>
        <el-button @click="save" type="primary" class="button">保存</el-button>
      </div>
      <div class="tool-bar"></div>
      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item
          :title="panelDsl.title"
          :name="panelDsl.title"
          v-for="(panelDsl, panelDslIndex) in dslArrayAdd"
          :key="'panelDsl'+panelDslIndex"
        >
          <c-table
            class="box-card"
            v-model="currentModelAdd"
            :dsl="panelDsl.rows"
            :rules="rules"
            :tableInfo="panelDsl"
            ref="topTable"
          />
        </el-collapse-item>
      </el-collapse>

      <el-collapse v-model="activeNames" @change="handleChange">
        <!-- title 用于显示在页面上，name用于展开的collapse与activeNames配合 -->
        <el-collapse-item title="影像列表" name="img-list-panel">
          <div id="img-list-panel">
            <c-image-list
              v-if="currentImgListModelAdd"
              v-model="currentImgListModelAdd"
              @whenUploaded="handleUploaded"
              :options="imageListOptions"
              ref="imgUpload"
            ></c-image-list>
          </div>
        </el-collapse-item>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import { dslArrayAdd } from "./mock.js";
let rules = {};
export default {
  name: "HqbjHjYxjcAdd",
  mixins: [Main],
  mounted() {
    /* get dslArrayAdd model from remote server
        and then set this.currentModelAdd this.dslArrayAdd
        */
    this.activeNames = this.dslArrayAdd.map(panel => {
      return panel.title;
    });
    this.activeNames.push("img-list-panel");
  },
  data() {
    /*  let r = (rule, value, callback) => {
          if (value === "") {
            this.$notify({ title: "请再次输入密码" });
            callback(new Error());
          } else {
            callback();
          }
        }; */
    return {
      /* rules: { ...rules, jcdh: [{ validator: r, trigger: "blur" }] }, */
      rules,
      dslArrayAdd,
      imageListOptions: { action: "/proxy/upload" },
      /* 动态数据 */
      activeNames: []
    };
  },

  methods: {
    goTo(command) {
      if (command === "home") {
        this.$confirm("是否放弃编辑?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$router.push({ name: command });
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    save() {
      /* topTable 是对c-table的ref的引用 */
      this.$refs.topTable[0].valid().then(isAllOk => {
        /* 第一步：先上传图片，完成后=》handleUploaded */
        if (isAllOk) {
          return this.$refs.imgUpload.submit();
        } else {
          this.$message({
            showClose: true,
            message: "请按照提示检查输入项",
            type: "warning"
          });
        }
      });
    },
    handleUploaded(imglist) {
      debugger;
      const loading = this.$loading();
      let doc = { ...this.currentModelAdd, imglist };
      debugger;
      console.log("this.currentModelAdd");
      /* save */
      this.$post("/proxy/save", {
        coll: "hqbj_hj_yxjc" /* collection */,
        doc /* document */
      }).then(response => {
        if (response.isSuccess) {
          /* 如果是修改，需要更新currentModelAdd */
          if (this.$route.name === "update") {
            this.setCurrentModelAdd(doc);
          }
          loading.close();
          this.$router.push({ name: "home" });
          this.$message({
            showClose: true,
            message: "保存成功",
            type: "success"
          });
        } else {
          this.$message({
            showClose: true,
            message: "保存失败",
            type: "warning"
          });
        }
      });
    }
  }
};
</script>

