        "children": [{
                "id": "0000000000000026",
                "text": "阴道PH值",
                "yz": "1",
                "dx_bzz": "4.5",
                "dw": "",
                "bzz": "4-6",
                "zlx": "number",
                "zfw": "",
                "ywsx": "PH",
                "dl_bzzfw1": "4.00",
                "dl_bzzfw2": "5.00",
                "lcyy": "阴道内呈弱酸性，这是阴道的自净作用，可以防止致病菌繁殖。如果患有滴虫性或细菌性阴道炎时，pH值上升,可大于5～6",
                "ty": "False"
            },
            {
                "id": "0000000000000027",
                "text": "阴道清洁度",
                "yz": "1",
                "dx_bzz": "I",
                "dw": "",
                "bzz": "I-IV",
                "zlx": "list",
                "zfw": "I,II,III,IV",
                "ywsx": "Y",
                "dl_bzzfw1": "",
                "dl_bzzfw2": "",
                "lcyy": "I～II度属正常,III～IV度为异常白带，表示阴道有炎症",
                "ty": "False"
            },
            {
                "id": "0000000000000028",
                "text": "阴道微生物",
                "yz": "1",
                "dx_bzz": "-",
                "dw": "",
                "bzz": "--++++",
                "zlx": "list",
                "zfw": "-,+,++,+++,++++",
                "ywsx": "W",
                "dl_bzzfw1": "",
                "dl_bzzfw2": "",
                "lcyy": "正常情况：无。一般会有真菌、滴虫、淋球菌等项，如果有，则在结果上标示“+”（阳性），没有就是“-”（阴性）。",
                "ty": "False"
            },
            {
                "id": "0000000000000029",
                "text": "胺试验",
                "yz": "1",
                "dx_bzz": "-",
                "dw": "",
                "bzz": "--++++",
                "zlx": "list",
                "zfw": "-,+,++,+++,++++",
                "ywsx": "A",
                "dl_bzzfw1": "",
                "dl_bzzfw2": "",
                "lcyy": "患有细菌性妇科炎症，白带可发出鱼腥味，由于厌氧菌产生的胺遇氢氧化钾释放氨气所致",
                "ty": "False"
            },
            {
                "id": "0000000000000030",
                "text": "线索细胞",
                "yz": "1",
                "dx_bzz": "4.5",
                "dw": "",
                "bzz": "--++++",
                "zlx": "list",
                "zfw": "-,+,++,+++,++++",
                "ywsx": "X",
                "dl_bzzfw1": "4.00",
                "dl_bzzfw2": "5.00",
                "lcyy": "细菌性阴道炎的最敏感、最特异的体征，根据胺试验阳性及有线索细胞即可做出细菌性阴道炎的诊断",
                "ty": "False"
            }
        ]
