import {
    mapState,
    mapMutations
} from "vuex";

export default {
    computed: {
        ...mapState(["currentModel", "currentModelAdd", "currentImgListModel", "currentImgListModelAdd"]),
        treeOptions() {
            let treeOptions = {
                props: {
                    children: "children",
                    label: "text"
                }
            };
            treeOptions.data = this.asideTree;
            return treeOptions;
        }
    },
    methods: {
        ...mapMutations(["setCurrentModel", "setCurrentModelAdd", "setcurrentImgListModel", "setcurrentImgListModelAdd"]),
        deleteTableRow(row, index) {
            console.log("deleteTableRow", row, index);
        },
        console(...args) {
            console.log(args);
        },
        submit() {
            this.$refs.hytable[0].valid().then(isSuccess => {
                if (isSuccess) {
                    this.$notify({
                        title: "提示",
                        message: "数据校验成功，可以提交表单"
                    });
                }
            });
        },
        handleChange(activeNames) {
            console.log("activeNames", activeNames, activeNames === this.activeNames);
        }
    },
    watch: {
        model(newV, oldV) {
            console.log("model change", newV, oldV);
        }
    },
    components: {
        // CPanelItem: () => import(/* webpackChunkName: "PanelItem" */ "@c/views/PanelItem.vue"),
        "c-table": () =>
            import( /* webpackChunkName: "FormTable" */ "@c/FormTable.vue"),
        CPickupTree: () =>
            import( /* webpackChunkName: "PickupTree" */ "@c/PickupTree.vue"),
        CImageList: () =>
            import( /* webpackChunkName: "ImageList" */ "@c/ImageList.vue")
    },
    beforeRouteEnter(to, from, next) {
        console.log("beforeRouteEnter", to, from, next);
        next();
        // 在渲染该组件的对应路由被 confirm 前调用
        // 不！能！获取组件实例 `this`
        // 因为当守卫执行前，组件实例还没被创建
    },
    beforeRouteUpdate(to, from, next) {
        console.log("tbeforeRouteUpdate", to, from);
        // 在当前路由改变，但是该组件被复用时调用
        // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
        // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
        // 可以访问组件实例 `this`
        next();
    },
    beforeRouteLeave(to, from, next) {
        console.log("beforeRouteLeave", to, from);
        // 导航离开该组件的对应路由时调用
        // 可以访问组件实例 `this`
        next();
    }
}